* {
    box-sizing: border-box;
}
html, body {
    margin: 0em;
    padding: 0em;
    height: 100%;
    min-width: 100%;
    background-color: #ecedef; 
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
#logo {
    position: absolute;
    top: 0em;
    left: 0em;
    margin: 0;
    padding: 0.5em;
    padding-top: 0.7em;
    font-size: 2em;
    font-family: 'Titan One';
}
#logo > a {
    text-decoration: none;
    color: #ecedef;
}
#logo > a:visited {
    text-decoration: none;
    color:#ecedef;
}

header {
    color:#ecedef;
    background-color: #192e2f;
}
nav {
    z-index: -1;
    background-color: #192e2f;
    width: 100%;
    text-align: right;
}

.nav-list {
    list-style-type: none;
    margin: 0em;
    margin-right: 2em;
}

.nav-list > li{
    display: inline-block;
    padding: 0.5em 0.8em;
    text-align: center;
    font-size: 1.5em;
}
.nav-list > li:hover {
    background-color: #bcd9d7;

}
.nav-list > li > a {
    text-decoration: none;
    color: #ecedef;
}

.nav-list > li > a:visited {
    text-decoration: none;
    color: #ecedef;
}
.nav-list > li:hover > a {
    color:black;
}
#content-container {
    display:flex;
    flex-direction:column;
    justify-content: space-between;
    min-height: 100vh;
}

#content-container > div {
    min-height: 100%;
}
footer {
    position: relative;
    background-color: #192e2f;
    color: #ecedef;
    height: 10em;
}
footer > div {
    display: flex;
    justify-content: space-around;
    align-content: space-around;
    height: 100%;
}
footer > div > ul {
    list-style-type: none;
    margin: 0em;
    padding: 0em;
    align-self: center;
}

footer > div > ul > li > a {
    text-decoration: none;
    font-weight: bold;
    font-size: 1.1em;
    color: #ecedef;
}
footer > div > ul > li > a:hover {
    color: #ecedef;
}

.copyright {
    position: absolute;
    bottom: 0em;
    text-align: center;
    width: 100%;
    margin: 0;
    padding: 0;
}
@media(max-width: 940px) {
    .nav-list > li{
        padding: 0.5em 0.2em;
    }
}
@media(max-width: 830px) { 

    #logo {
        position: static;
        font-size: 1.5em;
        width: 100%;
        padding: 0.5em 0em;
        text-align: center;
    }

    nav {
        text-align: center;
    }
    .nav-list {
        padding-left: 0em;
        padding-right: 0em;
        margin-left: 0em;
        margin-right: 0em;
        width: 100%;
    }
    .nav-list > li{
        font-size: 1.2em;
    }
}

@media(max-width: 350px) {


    .nav-list > li{
        display: block;
        padding-left: 0em;
        padding-right: 0em;
        width: 100%;
    }
}

button {
    background-color:  #ecedef;
    color:black;
    border-radius: 0.3em;
    padding: 0.5em 0.8em;
    margin: 0.2em;
    border: none;
    font-weight: bold;
}
button:active {
    background-color:#3a595c;
    color: white;
}

.success-container {
    text-align: center;
}
.form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

form {
    display: inline-block;
    padding: 2em;
    margin: 2em;
    text-align: center;
    border-radius: 0.2em;
    background-color:#192e2f;
    color:white;
    
}
form > div {
    text-align: right;
    padding-right: 3em;
}

.error-border {
    border: 2px solid red;
}
.error-message {
    color:red;
    font-size: 0.6em;
}

.hide-error {
    display: none;
}

.align-right {
    text-align: right;
}


.header-indent {
    width: 80%;
    margin: auto;
    padding: 0.5em;
}

.table-container {
    width: 80%;
    margin: auto;
    padding: 2em;
}

.card-container-layout {
    width: 80%;
    margin: auto;
    padding: 2em;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(20em, 1fr));
}
.card-container {
    background-color:  #bcd9d7;
    padding: 2em;
    border-radius: 0.2em;
    margin: 1em;
}
.light-card {
    background-color:  #ecedef;
}
.card-container  > textarea{
    width: 80%;
    min-height: 10em;
}
.confirm-button {
    background-color: #425f4b;
    color: white;
}
.confirm-button:active {
    background-color: #192e2f;
    color:white;
}

.request-submitted {
    color:green;
    font-weight: bold;
    font-style: italic;
}

.login-message {
    color:darkred;
    font-weight: bold;
    font-style: italic;
}

.fade-out-screen {
    background-color: rgba(0,0,0,0.9);
    height: 100%;
    width: 100%;
    position:fixed;
    top: 0em;
    left: 0em;
    z-index: 100;
    overflow: auto;
}

.close-pane {
    color: black;
    position: absolute;
    top: 1em;
    right: 1em;
    font-size: 2em;
}

.centered-content {
    text-align: center;
}

.make-scrollable {
    overflow: auto;
}

.heading-button {  
    display: block;
    background-color: #192e2f;
    width: 80%;
    margin: 1em auto;
    text-decoration: none;
    color:#ecedef;
    font-size: 1.3em;
    padding: 1em;
    font-weight: bold;
    border-radius: 0.5em;
}
.heading-button:visited {
    background-color: #192e2f;
}
.heading-button:active {
    background-color:  #bcd9d7;
    color: #192e2f;
}


@media(max-width: 400px) {

    form > div {
        text-align: center;
        padding-right: 0em;
    }
    .card-container-layout {
        width: 100%;
        padding: 0em;
    }
    .card-container  > textarea{
        width: 100%;
    }
}
.hero-image {
    padding-top: 10em;
    padding-bottom: 10em;
    background-image: url(/community-helper/static/media/workplace-1245776_1280.605a9464.jpg);
    background-repeat: no-repeat;
    background-position: center; 
    background-size: cover;

}
.hero-image > h1 {
    text-align: center;
    margin: 0em;
    font-size: 4em;
    font-weight: bold;
    padding-top: 4em;
    text-shadow: 2px 2px 4px black;
    color:white;
}
.landing-style > h2 {
    text-align: center;
    margin-top: 5em;
    margin-bottom: 5em;
    padding-left: 25%;
    padding-right: 25%;
}
.content-card {
    padding: 8em 1em;
    display: flex;
}

.content-card > h3 {
    margin: 0em;
    padding: 0em;
    width: 50%;
    display:inline-block;
    text-align: center;
    padding: 3em;
    text-shadow: 2px 2px 4px black;
    color:white;
    font-size: 2em;
}
.content-card > p {
    margin: 0em;
    padding: 0em;
    width: 50%;
    display:inline-block;
    padding: 5em;
    font-size: 1.5em;
}

.content-card:nth-child(odd) {
    background-color:#bcd9d7;
}

@media(max-width: 830px) { 
    .content-card {
        flex-flow: column wrap;
    }
    
    .content-card > h3 {
        padding-left: 0em;
        padding-right: 0em;
        width: 100%;
    }
    .content-card > p {
        padding-left: 0em;
        padding-right: 0em;
        width: 100%;
    }

}




